<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process244"></a>

<p><br></p>
<p><span class="rvts17">淹没的神庙（</span><span class="rvts50">The Drown Temple</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:136,Y:900</span><span class="rvts21">，地点：被淹没的神庙（</span><span class="rvts20">The Drown Temple</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">阻止黑环法师在海洋处的魔法活动，使兽人神庙区域水位下降，并探索神庙</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1361.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">在兽人神庙这里，有两个黑环法师把守，尝试说法他们或打败他们。此时，兽人神庙处于</span><span class="rvts20"></span><span class="rvts21">被淹</span><span class="rvts20"></span><span class="rvts21">状态，我们无法正常进入。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1362.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">找到海边黑环营地的黑环队长，尝试说服队长停止石岩处的黑环法师吟唱魔法。注意，说服吟唱的黑环法师是不会停止她的施法的，必须说服他们的队长才行。如果说服失败，那么打败他们停止施法。</span>
<br><span class="rvts21">无论说服还是打败他们，之后兽人神殿的水位会下降，大门会突显出来。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1363.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">进入兽人神殿，在远处的一个骷髅处拿到一把开启神殿秘门的钥匙。注意，兽人神殿的陷阱比较多，注意躲避路面上的石板机关。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1364.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">使用钥匙打开大门。如果没有找到钥匙的话，使用队伍中力量较高的角色也可以成功拉动该秘门的开关闸刀。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1365.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">进入秘门，里面有口巨大的石棺，其中最里面的那口石棺拥有较多较好的装备。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



